.groupSectionWrapper{
  display: flex;
  height: 100%;
  width: 25%;
}
.notesListingWrapper{
  display: flex;
  height: 100%;
  width: 75%;
}

.createNewGroupSection{
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(47, 47, 47, 0.75);
}
.createGroupForm{
  width: 40%;
  height: 35%;
  background: white;
  display: flex;
  flex-direction: column;
  padding: 1% 2%;
  box-sizing: border-box;
}
.createGroupInputWrapper{
  display: flex;
  flex-direction: column;
  width: 100%; 
  height: 50%;
  justify-content: space-evenly;
  align-items: center;
}
.createGroupInputs{
  display: flex;
  align-items: center;
  width: 90%;
  justify-content: space-between;
}
.createGroupInputs label{
  font-weight: 600;
  font-size: 1.3rem;
}
.groupNameInput{
  padding: 2%;
  width: 50%;
  border: 2px solid #CCCCCC;
  border-radius: 22px;
}
.createGroupBtnWrapper{
  display: flex;
  justify-content: flex-end;
  height: 15%;
}
.createGroupFormBtn{
  background: #000000;
  border-radius: 11px;
  color: white;
  border: none;
  padding: 2% 6%;
}
.colorOptionsWrapper{
  display: flex;
  width: 50%;
}
.colorOption{
  width: 30px;
  height: 30px;
  border-radius: 50px;
  margin: 0px 4px;
  box-sizing: border-box;
}

@media(max-width:650px) {
  .groupSectionWrapper{
    width: 100%;
  }

  .createGroupForm{
    width: 85%;
    border-radius: 8px;
    padding: 5%;
  }
  .createGroupForm h2{
    font-size: 20px;
    margin: 4%;
  }
  .createGroupInputs{
    justify-content: flex-start;
  }
  .createGroupInputs label{
    font-size: 1rem;
    width: fit-content;
    margin-right: 2%;
    font-weight: 700;
    word-wrap: normal;
    width: 40%;
  }
  .groupNameInput{
    width: 60%;
  }
  .colorOptionsWrapper{
    width: 60%;
    flex-wrap: wrap;
  }
  .colorOption{
    width: 25px;
    height: 25px;
    margin: 2%;
  }
}